import React, {Component, ReactNode} from 'react';
import './App.css';
import {Headner} from "./widgets/headner";

import {HashRouter as Router, Redirect, Route, Switch, withRouter} from "react-router-dom";
import {HomePage} from "./pages/home/home";
import AboutPage from "./pages/about/about";
import {PortfolioPage} from "./pages/protfolio/protfolio";

class ScrollToTop extends Component {
    componentDidUpdate(prevProps: { location: any; }) {
        // @ts-ignore
        if (prevProps.location !== this.props?.location) {
            window.scrollTo(0, 0)
        }
    }

    render() {
        return this.props.children
    }
}

// @ts-ignore
const ScrollWap = withRouter(ScrollToTop);


function App() {



    return (
        <div className="App">
            <Router>
                <ScrollWap>
                    <Headner ac={0} opacity={0}/>
                    <Switch>
                        <Route path="/home/" component={HomePage}/>
                        <Route path="/about/" component={AboutPage}/>
                        <Route path="/portfolio/" component={PortfolioPage}/>
                        <Redirect to="/home"/>
                    </Switch>
                </ScrollWap>

            </Router>

        </div>
    );
}

export default App;
