export const ProjectWidget: React.FC = () => {
    const [ac, setAc] = useState(ProjectType.nil);
    return (
        <WapStyle>
            <div className="bar">
                <Hidden mdDown>
                    <Grid container spacing={9} alignItems={"stretch"} justify={"space-between"}>
                        {
                            pcBar.map(
                                (e, i) =>
                                    <Grid>
                                        <p key={'xxxaa' + i}
                                           className={ac === i ? "ac" : ""}
                                           onClick={() => setAc(i)}
                                        >{getTitleByType[e]}</p>
                                    </Grid>
                            )
                        }
                    </Grid>
                </Hidden>

                <Hidden lgUp>
                    <Grid container spacing={9} alignItems={"stretch"} justify={"space-between"}>
                        {
                            [0, 2, 3, 4, 6, 1, 5].map(
                                (e, i) =>
                                    <Grid>
                                        <p key={'xxxaa' + i}
                                           className={ac === i ? "ac" : ""}
                                           onClick={() => setAc(i)}
                                        >{getTitleByType[e]}</p>
                                    </Grid>
                            )
                        }
                    </Grid>
                </Hidden>


            </div>

            <Grid container spacing={5} alignItems={"stretch"}>
                {listData.map((ev, i) => {
                    if (ev.type !== ac && ac !== ProjectType.nil) {
                        return
                    }
                    return <ItemWidget index={i} key={"ssss" + i} itemData={ev}/>;
                })}
            </Grid>
        </WapStyle>
    );
}

const ItemWidget: React.FC<{ itemData: ItemData, index: number }> = ({itemData, index}) => {
    return (<Grid item xs={6} lg={3 } sm={4} md={4}>
            <ItemStyle
                index={index}
                itemData={itemData}
                bgImg={require('../../assets/project/img_' + index + '.png').default}
                onClick={() => {
                    window.open(itemData.url, "_blank")
                }}
            >
                <div className={'top'}>
                    <div className="img"

                         style={{backgroundImage: 'url(' + require(`../../assets/project_d/img_${index}.png`).default + ')'}}
                    >

                    </div>
                    <p>{itemData.text}</p>


                    {
                        itemData.isEquity && <div className="bar_img">
                            <img src={barImg} alt=""/>
                        </div>
                    }
                </div>
                <div className="bg"></div>
            </ItemStyle>
        </Grid>
    );
}


